.btn {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 13px 20px;
  font-size: 14px;
  color: $text-color-light;
  background: $akcent;
  border-radius: 5px;
  &:hover {
    cursor: pointer;
    background: lighten($akcent, 10); } }
h1 {
  font-size: 41px;
  color: $text-color-light;
  font-weight: bold;
  line-height: 42px;

  span {
    display: block;
    font-size: 28px;
    font-weight: normal; } }

.logo {
  margin-right: auto; }
