.header {
  &__top-wrap {
    padding-top: 59px;
    padding-bottom: 15px;
    display: flex;
    align-items: center; }
  &__contacts {
    display: flex;
    font-size: 22px;
    color: $text-color-dark;
    text-transform: uppercase; }
  &__phone {
    &:nth-child(1) {
      border-right: 1px solid #cfcfd0;
      padding-right: 14px;
      margin-right: 14px; } }
  &__call {
    margin-left: 30px;
    img {
      height: 20px;
      width: 20px;
      margin-right: 10px; } } }
